import React from "react";
import { Link } from "react-router-dom";
import { PrimaryBtn } from "../../Atoms/Btn/index";
import { Primary } from "../../Atoms/TextInput/index";

const Register: React.FC = (props) => {
  const onSubmitHandler = () => {
    checkForms();
  };
  const checkForms = () => {
    const email = document.querySelector(
      'input[name="email"]'
    ) as HTMLButtonElement;
    const errors = [];
    let isError: boolean = false;
    if (email.value === "") {
      let emailError = email.parentNode?.querySelector(".error-message")!;
      emailError.textContent = "";
      errors.push(true);
    }
    errors.forEach((error) => {
      if (error) {
        isError = true;
      }
    });
    if (!isError) {
      alert("OK!");
    }
  };

  return (
    <div className="register">
      <div className="formBox">
        <form method="POST" action="#" className="contentBox contentBox--big">
          <h1 className="heading4"></h1>
          <Primary
            type="email"
            name="email"
            ttl=""
            placeholder="example@gmail.com"
            isError={true}
            isRequired={true}
          />
          <PrimaryBtn
            type="button"
            txt=""
            Func={onSubmitHandler}
          />
          <p>
            
            <Link to="/login"></Link>
          </p>
        </form>
      </div>
    </div>
  );
};

export default Register;
