import React from "react";
import { TrashIcon } from "../../../assets/images/index";
import { SelectSecondary } from "../../Atoms/Input";
import { Secondary } from "../../Atoms/TextInput";
import { motion } from "framer-motion";

interface Props {
  id: number;
}

const StepSheet: React.FC<Props> = (props) => {
  const pageTransition = {
    in: {
      opacity: 1,
      x: 0,
      transition: {
        duration: 0.8,
      },
    },
    out: {
      x: 20,
      opacity: 0,
    },
  };
  const isError = [
    { isEmpty1: false },
    { isEmpty2: false },
    { isEmpty3: false },
  ];
  const calendarObj = [
    { value: "1" },
    { value: "2" },
    { value: "3" },
    { value: "4" },
    { value: "5" },
    { value: "6" },
    { value: "7" },
    { value: "8" },
    { value: "9" },
    { value: "10" },
    { value: "11" },
    { value: "12" },
  ];

  const deleteHandler = () => {
    const article = document.getElementById(`inputWindow-${props.id}`)!;
    if (props.id !== 1) {
      article.style.display = "none";
    }
  };

  return (
    <motion.article
      id={`inputWindow-${props.id}`}
      key={props.id}
      className="contentBox contentBox--big interview-formBox"
      initial="out"
      animate="in"
      exit="out"
      variants={pageTransition}
    >
      <h1 className="heading4"></h1>
      <div className="contentBox__wrap">
        <Secondary
          name="title"
          type="string"
          labelTxt=""
          isRequired={true}
          isRequiredTxt={true}
          placeholderTxt=") "
          isError={isError}
          isIcon={false}
        />
        <SelectSecondary name="date" ttl="" selectObj={calendarObj} />
      </div>
      <div className="input-txtarea">
        <p className="input-txtarea__heading"></p>
        <textarea placeholder=""></textarea>
      </div>
      {props.id !== 1 && (
        <div
          id={`inputWindowDelete-${props.id}`}
          className="btn btn--delete"
          onClick={deleteHandler}
        >
          <img src={TrashIcon} alt="" />
        </div>
      )}
    </motion.article>
  );
};

export default StepSheet;
