import React, { useEffect, useState } from "react";
import {
  MailIcon,
  DocumentIcon_White,
  CheckIcon,
} from "../../../assets/images/index";

import { SelectPrimary } from "../../Atoms/Input";
import { Primary } from "../../Atoms/TextInput";
import { CheckboxWithText } from "../../Molecules/Input";
import { ActionBtn } from "../../Atoms/Btn/index";

const RegisterInsert: React.FC = (props) => {
  useEffect(() => {
    selectBtnChanges();
  }, []);

  const selectBtnChanges = () => {
    const selectBtns = document.querySelectorAll(".select-btn");
    console.log(selectBtns);
    selectBtns.forEach((btn) => {
      btn.addEventListener("click", () => {
        selectBtns.forEach((item) => {
          item.classList.remove("current");
        });
        btn.classList.add("current");
      });
    });
  };

  const jobTypes = [
    "",
    "",
    "",
    "",
    "",
  ];

  const yearList = ["21", "22", "23"];

  const onSubmitHandler = () => {
    checkForms();
  };

  const isEmptyValidation = (
    el: any,
    errors: Array<boolean>,
    message: string
  ) => {
    let elError = el.parentNode?.querySelector(".error-message")!;
    if (el.value === "") {
      elError.textContent = `${message}`;
      errors.push(true);
    } else {
      elError.textContent = "";
    }
  };

  const checkInputLength = (
    el: HTMLInputElement,
    maxLength: number,
    errors: Array<boolean>,
    message: string
  ) => {
    let elError = el.parentNode?.querySelector(".error-message")!;
    if (el.value.length > maxLength) {
      elError.textContent = `${message}`;
      errors.push(true);
    } else if (el.value !== "") {
      elError.textContent = "";
    }
  };

  const passwordAgainCheck = (
    password: HTMLInputElement,
    passwordAgain: HTMLInputElement,
    errors: Array<boolean>,
    message: string
  ) => {
    let elError = passwordAgain.parentNode?.querySelector(".error-message")!;
    if (password.value !== passwordAgain.value) {
      elError.textContent = `${message}`;
      errors.push(true);
      console.log("!");
    }
  };

  const [image, setImage] = useState("");
  const onImageHandler = (event: any) => {
    const sendObj = {
      image: "",
    };
    const image = event.target.files[0];
    const imageUrl = URL.createObjectURL(image);
    setImage(imageUrl);
    const fr = new FileReader();
    fr.onloadend = function () {
      sendObj.image = String(fr.result);
    };
    fr.readAsDataURL(image);
  };

  const createSendData = () => {};

  const checkForms = () => {
    const student_number = document.querySelector(
      'input[name="student_number"]'
    ) as HTMLInputElement;
    const password = document.querySelector(
      'input[name="password"]'
    ) as HTMLInputElement;
    const passwordAgain = document.querySelector(
      'input[name="password_again"]'
    ) as HTMLInputElement;
    const lastName = document.querySelector(
      'input[name="last_name"]'
    ) as HTMLInputElement;
    const firstName = document.querySelector(
      'input[name="first_name"]'
    ) as HTMLInputElement;
    const errors: Array<boolean> = [];
    let isError: boolean = false;
    isEmptyValidation(student_number, errors, "");
    isEmptyValidation(password, errors, "");
    isEmptyValidation(
      passwordAgain,
      errors,
      ""
    );
    isEmptyValidation(lastName, errors, "");
    isEmptyValidation(firstName, errors, "");
    checkInputLength(
      student_number,
      10,
      errors,
      "10"
    );
    checkInputLength(password, 30, errors, "30");
    checkInputLength(passwordAgain, 30, errors, "30");
    checkInputLength(lastName, 30, errors, "30");
    checkInputLength(firstName, 30, errors, "30");
    passwordAgainCheck(
      password,
      passwordAgain,
      errors,
      ""
    );
    errors.forEach((error) => {
      if (error) {
        isError = true;
      }
    });
    if (!isError) {
      alert("OK!");
    }
  };
  return (
    <div className="register">
      <ul className="register-step">
        <li className="current">
          <img src={MailIcon} alt="" />
          <span></span>
        </li>
        <li>
          <img src={DocumentIcon_White} alt="" />
          <span></span>
        </li>
        <li>
          <img src={CheckIcon} alt="" />
          <span></span>
        </li>
      </ul>

      <div className="formBox">
        <form method="POST" action="#">
          <div className="contentBox contentBox--big">
            <h1 className="heading4">
              <span>*</span>
            </h1>
            <p></p>
            <p>example@gmail.com</p>
            <Primary
              name="student_number"
              type="number"
              ttl=""
              placeholder=""
              isRequired={true}
              isError={true}
            />
            <Primary
              name="password"
              type="password"
              ttl=""
              placeholder="8"
              isRequired={true}
              isError={true}
            />
            <Primary
              name="password_again"
              type="password"
              ttl=""
              placeholder="8"
              isRequired={true}
              isError={true}
            />
          </div>

          <div className="contentBox contentBox--big">
            <h1 className="heading4"></h1>
            <p></p>
            <p>example@gmail.com</p>
            <Primary
              type="text"
              ttl=""
              placeholder=""
              isRequired={true}
              name="last_name"
              isError={true}
            />
            <Primary
              type="text"
              ttl=""
              placeholder=""
              isRequired={true}
              name="first_name"
              isError={true}
            />
            <div className="gender-select">
              <p className="gender-select__heading"></p>
              <ul className="gender-select-list">
                <li>
                  <label className="btn select-btn current">
                    <span></span>
                    <input type="checkbox" name="gender" />
                  </label>
                </li>
                <li>
                  <label className="btn select-btn">
                    <span></span>
                    <input type="checkbox" name="gender" />
                  </label>
                </li>
                <li>
                  <label className="btn select-btn">
                    <span></span>
                    <input type="checkbox" name="gender" />
                  </label>
                </li>
              </ul>
            </div>

            <p></p>
            <input type="file" onChange={onImageHandler} />
            <img className="image-preview" src={image} alt="" />

            <div className="label-input">
              <p className="label-input__txt">
                <span className="cAttention">*</span>
              </p>
              <SelectPrimary name="job" options={jobTypes} required={true} />
            </div>

            <div className="label-input">
              <p className="label-input__txt">
                <span className="cAttention">*</span>
              </p>
              <SelectPrimary
                name="graduation_year"
                options={yearList}
                required={true}
              />
            </div>

            <div className="label-input">
              <p className="label-input__txt">
                <span className="cAttention">*</span>
              </p>
              <p>
                
              </p>
            </div>

            <CheckboxWithText
              type="checkbox"
              txt=""
            />

            <ActionBtn
              type="submit"
              txt=""
              isPlus={false}
              linkUrl="#"
              clickFunc={onSubmitHandler}
            />
          </div>
        </form>
      </div>
    </div>
  );
};

export default RegisterInsert;
